<template>
  <div>
    <p class="mb-1">Дата:</p>
    <v-menu
      v-model="fromDateMenu"
      :close-on-content-click="false"
      :nudge-right="40"
      class="mt-2"
      transition="scale-transition"
      offset-y
      max-width="290px"
      min-width="290px"
    >
      <template v-slot:activator="{ on }">
        <v-text-field
          :id="dateFilterId"
          label="Дата"
          outlined
          solo
          append-icon="mdi-calendar"
          single-line
          dense
          hide-details
          readonly
          :value="fromDateDisp"
          v-on="on"
          @click:append="showC"
        ></v-text-field>
      </template>
      <v-date-picker
        v-model="fromDateVal"
        no-title
        @input="fromDateMenu = false"
        first-day-of-week="1"
      ></v-date-picker>
    </v-menu>
  </div>
</template>

<script>
export default {
  name: 'DateFilter',
  props: ['dateFilterId'],
  data: (vm) => ({
    isShow: false,
    fromDateVal: new Date().toISOString().substr(0, 10),
    dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
    fromDateMenu: false
  }),
  computed: {
    fromDateDisp() {
      return this.formatDate(this.fromDateVal);
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split('-');
      return `${day}.${month}.${year}`;
    },
    showC() {
      this.fromDateMenu = !this.fromDateMenu;
    }
  }
};
</script>

<style scoped></style>
