<template>
  <v-card v-resize="onResize" id="table-norm-section">
    <v-data-table
      :headers="headersFlat"
      :items="tableData"
      dense
      class="mt-3 pa-2"
      disable-pagination
      disable-filtering
      disable-sort
      hide-default-footer
      hide-default-header
      locale="ru-RU"
      :height="windowSize.y - 149"
      calculate-widths
      loading-text="Загрузка..."
    >
      <template #header="{}">
        <thead class="v-data-table-header">
          <tr>
            <th
              v-for="(h, i) in headers"
              :key="i"
              class="text-center parent-header td-border-style"
              :rowspan="h.children ? 1 : 3"
              :colspan="getColspan(h)"
              ref="headerCell"
            >
              {{ h.text }}
            </th>
          </tr>
          <tr>
            <th
              v-for="(h1, i1) in getSubHeader(headers)"
              :key="i1"
              class="text-center child-header td-border-style"
              :rowspan="h1.children ? 1 : 2"
              :colspan="h1.children ? h1.children.length : 1"
            >
              {{ h1.text }}
            </th>
          </tr>
          <tr>
            <th
              v-for="(h2, i2) in getSubHeader(headers, true)"
              :key="i2"
              class="text-center child-header td-border-style"
              :rowspan="h2.children ? 1 : 2"
              :colspan="h2.children ? h2.children.length : 1"
            >
              {{ h2.text }}
            </th>
          </tr>
        </thead>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import axios from 'axios';
export default {
  name: 'DataTable',
  created() {
    this.fetchTableData();
  },
  methods: {
    fetchTableData() {
      axios.get(this.url).then((response) => {
        this.tableData = response.data.data;
      });
    },
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    },
    getSubHeader(headers, deeper) {
      let result = [];
      headers
        .filter((i) => i.children)
        .forEach((v) => {
          result = result.concat(v.children);
        });
      if (deeper) {
        let subChild = [];
        result
          .filter((l) => l.children)
          .forEach((b) => {
            subChild = subChild.concat(b.children);
          });
        return subChild;
      }
      return result;
    },
    getColspan(col) {
      let colspan;
      if (col.cols) {
        colspan = col.cols;
      } else colspan = col.children ? col.children.length : 1;
      return colspan;
    }
  },
  props: ['headers', 'headersFlat', 'url'],
  data() {
    return {
      windowSize: {
        x: 0,
        y: 0
      },
      tableData: []
    };
  }
};
</script>

