<template>
  <div>
    <p class="mb-1">Район управления ДРЛ:</p>
    <v-select
      :items="drls"
      item-text="name"
      item-value="id"
      outlined
      solo
      dense
      hide-details
    ></v-select>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'DrlFilter',
  created() {
    this.fetchDrl();
  },
  methods: {
    fetchDrl() {
      axios.get('server/drl.json').then((response) => {
        this.drls = response.data.data;
      });
    }
  },
  data() {
    return {
      drls: []
    };
  }
};
</script>
<style scoped></style>
