import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import NormalizationSections from '../views/NormalizationSections';
import NormalizationDepo from '../views/NormalizationDepo';
import NormalizationRatio from '../views/NormalizationRatio';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/norm_section',
    name: 'NormalizationSections',
    component: NormalizationSections
  },
  {
    path: '/norm_depo',
    name: 'NormalizationDepo',
    component: NormalizationDepo
  },
  {
    path: '/norm_ratio',
    name: 'NormalizationRatio',
    component: NormalizationRatio
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router;
